.header{
    display: flex;
    justify-content: space-between;
    align-items:center;
    padding: 1rem;
    position:fixed;
    width: 100%;
    height: 90px;
    z-index: 3;
}
.header-bg{
    background-color: rgba(0,0,0,0.8);
    transition: 0.3s;
}
.nav-menu{
    display:flex;
    
}
.nav-menu li{
    padding: 0 1rem;
}
.nav-menu li a{
   font-size: 20;
   font-family: sans-serif;
   font-weight: bold;
}

.hamburger{
    display: none;
    z-index: 4;
}
@media screen and (max-width:1040px){
    .nav-menu{
        flex-direction: column;
        justify-content: center;
        position: absolute;
        align-items: center;
        width: 100%;
        height: 100vh;
        background: rgba(0,0,0,0.9);
        top:0;
        left:-100%;
        z-index: -3;
        transition:0.1s;
    }
    .nav-menu.active{
        left: 0;
    }
    .nav-menu li{
        padding: 1rem 0;
    }
    .nav-menu li a{
        font-size: 20;
    }
    .hamburger{
        display: initial;
    }
}